<template>
	<div id="paybill-index">
		<public-header title="账单"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.pay_type == 'all'}" @click="onToggleStatus('all')">全部</div>
					<div class="tab-item" :class="{active: filter.items.pay_type == 'wechat'}" @click="onToggleStatus('wechat')">微信</div>
					<div class="tab-item" :class="{active: filter.items.pay_type == 'alipay'}" @click="onToggleStatus('alipay')">支付宝</div>
					<div class="tab-item" :class="{active: filter.items.pay_type == 'credit'}" @click="onToggleStatus('credit')">余额</div>
				</div>
				<van-search placeholder="请输入用户昵称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="paybill-list"
			>
				<div class="paybill-item" v-for="(item, index) in records.data">
					<router-link :to="util.getUrl({path: '/pages/paybill/detail', query:{id: item.id}})">
						<div class="border-1px-b padding-10 flex-lr">
							<div class="c-default font-16">#{{item.serial_sn}}</div>
							<div class="font-16" :class="{'c-primary': item.pay_type == 'wechat', 'c-info': item.pay_type == 'alipay', 'c-danger': item.pay_type == 'credit'}">{{item.pay_type_cn}}</div>
						</div>
						<div class="border-1px-b padding-10 flex-lr">
							<div class="flex">
								<div class="paybill-img">
									<img class="img-100" :src="item.avatar" alt="">
								</div>
								<div>{{item.nickname}}</div>
							</div>
							<div class="price">
								<div class="font-14">顾客支付：¥{{item.final_fee}}</div>
								<div class="font-14">最终收入：¥{{item.store_final_fee}}</div>
							</div>
						</div>
						<div class="padding-10">
							<div class="font-12 c-gray flex">
								<div>订单编号：</div>
								<div>{{item.order_sn}}</div>
							</div>
							<div class="font-12 c-gray flex padding-5-t">
								<div>下单时间：</div>
								<div>{{item.addtime_cn}}</div>
							</div>
						</div>
					</router-link>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						pay_type: 'all'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					recordsName: 'orders',
					url: 'manage/paybill/index/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				if(this.filter.items.pay_type == value) {
					return;
				}
				this.filter.items.pay_type = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#paybill-index .content .paybill-item{
		margin-bottom: 10px;
		background: #fff;
	}
	#paybill-index .content .paybill-item .paybill-img{
		width:35px;
		height:35px;
		border:none;
		margin-right:10px;
	}
	#paybill-index .content .paybill-item .price{
		text-align: right;
	}
	#paybill-index .content .paybill-item .price div{
		line-height: 21px;
	}
</style>


